      section_model: 0,
      mammography_model: 0,
      electrotherapy_model: 0
    };
  },
  components: {
    Cancer
  },
  methods: {
    times_function() {
      var itms = [];
      var i;
      for (i = 1; i < 25; i++) {
        itms.push({ item_id: i, item_value: i });
      }
      this.times = itms;
    }
  },

  mounted() {
    this.times_function();
  }
};
