<template>
  <div>
    <v-list-item>
      <v-list-item-content>
        <v-list-item-title>{{rb2s_title}}</v-list-item-title>
        <v-radio-group v-model="model">
          <v-radio :name="rb2radio_name" label="否" color="primary" value="0"></v-radio>
          <v-radio :name="rb2radio_name" label="是" color="primary" value="1"></v-radio>
        </v-radio-group>
        <div v-show="model==1">
          <v-select
            :name="rb2select_name"
            v-model="test_bind_v"
            item-text="item_id"
            item-value="item_value"
            :items="item"
            :label="rb2s_label"
            solo
          ></v-select>
          <input type="hidden" :name="rb2pass_name" :value="test_bind_v" />
        </div>
      </v-list-item-content>
    </v-list-item>

    <v-divider></v-divider>
  </div>
</template>
<script>
export default {
  data() {
    return {
      model: null,
      item: [],
      test_bind_v: 0
    };
  },

  props: {
    rb2s_title: String,
    rb2radio_name: String,
    rb2s_label: String,
    rb2select_name: String,
    rb2pass_name: String
  },
  methods: {
    item_function() {
      var itms = [];
      var i;
      for (i = 1; i < 25; i++) {
        itms.push({ item_id: i, item_value: i });
      }
      this.item = itms;
    }
  },
  mounted() {
    this.item_function();
  }
};
</script>